import { NgFor, NgIf } from '@angular/common';
import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { PageMetaData } from '@core/models';
import { IconService } from '@core/services/icon.service';
import { ViewportService } from '@core/services/viewport.service';
import { CommonUtil } from '@core/utils/common.util';

@Component({
    imports: [MatIconModule, RouterModule, NgFor, NgIf],
    selector: 'findqo-my-ad-list-pagination',
    templateUrl: './pagination.component.html',
    styleUrls: ['./pagination.component.scss']
})
export class MyAdListPaginationComponent implements OnChanges, OnInit {
	constructor(
		public viewport: ViewportService,
		public icon: IconService
	) {}

	@Input() pageMetaData: PageMetaData;

	@Output() pageChange = new EventEmitter<number>();

	perPage: number;
	totalPages: number;
	currentPage: number;
	total: number;
	pages: number[] = [];

	prevPage(): void {
		if (this.currentPage > 1) {
			this.currentPage--;
			this.updatePageNumbers();
			this.pageChange.emit(this.currentPage);
		}
	}

	nextPage(): void {
		if (this.currentPage < this.totalPages) {
			this.currentPage++;
			this.updatePageNumbers();
			this.pageChange.emit(this.currentPage);
		}
	}

	gotoPage(page: number): void {
		if (page >= 1 && page <= this.totalPages && this.currentPage !== page) {
			this.currentPage = page;
			this.updatePageNumbers();
			this.pageChange.emit(this.currentPage);
		}
	}

	private updatePageNumbers(): void {
		this.pages = [];
		let startPage = Math.max(1, this.currentPage - 2);
		const endPage = Math.min(this.totalPages, startPage + 4);

		// Adjust startPage if we're near the end to always show 5 pages
		if (endPage - startPage < 4) {
			const adjustment = 4 - (endPage - startPage);
			if (startPage - adjustment > 0) {
				startPage -= adjustment;
			}
		}

		for (let i = startPage; i <= endPage; i++) {
			this.pages.push(i);
		}
	}

	private setPageMetaData(pageMetaData: PageMetaData): void {
		if (pageMetaData) {
			this.currentPage = Number.parseInt(pageMetaData?.currentPage);
			this.totalPages = Number.parseInt(pageMetaData?.totalPages);
			this.total = Number.parseInt(pageMetaData?.total);
			this.perPage = Number.parseInt(pageMetaData?.perPage);
			this.totalPages = Math.ceil(this.total / this.perPage);
			this.updatePageNumbers();
		}
	}

	ngOnInit(): void {
		this.setPageMetaData(this.pageMetaData);
	}

	ngOnChanges(changes: SimpleChanges): void {
		CommonUtil.handleNgOnChanges(changes['pageMetaData'], (pageMetaData: PageMetaData) => {
			this.setPageMetaData(pageMetaData);
		});
	}
}
