import { MatDialog } from '@angular/material/dialog';
import { Component, Input } from '@angular/core';
import { Translations } from '@core/models/translations.model';
import { ViewportService } from '@core/services/viewport.service';
import { IconService } from '@core/services/icon.service';
import { MyAdDeleteDialogComponent } from '../my-ad-delete-dialog/my-ad-delete-dialog.component';
import { MyAdShareDialogComponent } from '../my-ad-share-dialog/my-ad-share-dialog.component';
import { Ad } from '@core/models';
import { BrowserUtility } from '@core/services/browser-utility.service';
import { WindowService } from '@core/services/window.service';
import { CommonFacade } from '@core/facades/common.facade';
import { MyAdUnPublishDialogComponent } from '../my-ad-unpublish-dialog/my-ad-unpublish-dialog.component';

@Component({
    selector: 'findqo-my-ad-item-actions',
    templateUrl: './my-ad-item-actions.component.html',
    styleUrls: ['./my-ad-item-actions.component.scss'],
    standalone: false
})
export class MyAdItemActionsComponent {
	constructor(
		public viewport: ViewportService,
		public icon: IconService,
		private matDialog: MatDialog,
		private readonly window: WindowService,
		private readonly utility: BrowserUtility,
		private readonly commonFacade: CommonFacade
	) {}

	isPaymentPending = false;
	isDraft = false;
	isUnpublish = false;
	isPublish = false;
	@Input() translations: Translations;
	@Input() ad: Ad;

	deleteAd(): void {
		this.matDialog
			.open(MyAdDeleteDialogComponent, {
				disableClose: true,
				maxWidth: '453px',
			})
			.afterClosed();
	}
	unpublishAd(): void {
		this.matDialog
			.open(MyAdUnPublishDialogComponent, {
				disableClose: true,
				maxWidth: '453px',
			})
			.afterClosed();
	}
	publishAd(): void {
		this.matDialog
			.open(MyAdUnPublishDialogComponent, {
				disableClose: true,
				maxWidth: '453px',
			})
			.afterClosed();
	}

	shareAd(): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		const AVALABLE_FROM = this.ad.availableFrom ? TRANSLATIONS?.['n3111'] + ': ' + this.commonFacade.getFormattedDate(this.ad.availableFrom) : '';

		if (this.utility.isWebShareSupported()) {
			navigator
				.share({
					title: this.ad?.title,
					text: `${this.ad?.title}\n${TRANSLATIONS?.['n152']}:  ${+this.ad.price.isPriceOnApplication ? TRANSLATIONS?.['n3311'] : this.ad.price.symbol + this.ad.price.minValue + ' ' + (this.ad.price.maxValue ? ' - ' + this.ad.price.maxValue : '')}\n${AVALABLE_FROM}\n${TRANSLATIONS?.['n3953']}\n`,
					url: this.ad?.adlink ?? this.window.reference.location.href,
				})
				.then(() => {
					// TODO: Do we need snackbar here?
					console.log('Content shared successfully!');
				})
				.catch((error) => {
					console.error('Error sharing content:', error);
				});
		} else {
			this.matDialog
				.open(MyAdShareDialogComponent, {
					disableClose: true,
					maxWidth: '375px',
					data: { myAdLink: this.ad.adlink },
				})
				.afterClosed();
		}
	}

	ngOnInit(): void {
		this.isDraft = this.ad.adStatus.status === 'draft';
		this.isUnpublish = this.ad.adStatus.status === 'unpublished';
		this.isPublish = this.ad.adStatus.status === 'live';
	}
}
