import { AfterViewInit, Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { SnackService } from '@core/services/snack.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ad-share-dialog',
    templateUrl: './my-ad-share-dialog.component.html',
    styleUrls: ['./my-ad-share-dialog.component.scss'],
    standalone: false
})
export class MyAdShareDialogComponent implements AfterViewInit {
	constructor(private snackBarService: SnackService, @Inject(MAT_DIALOG_DATA) public data: string, private commonFacade: CommonFacade) {}

	@ViewChild('adLink') adLink!: ElementRef<HTMLInputElement>;

	readonly iconType = IconType;
	readonly buttonType = ButtonType;

	copyAdLink(adLink: string): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		navigator.clipboard.writeText(adLink);

		this.snackBarService.open({
			data: { message: TRANSLATIONS['n227'], type: 'success' },
			panelClass: 'snackbar-success',
		});
	}

	ngAfterViewInit(): void {
		this.adLink.nativeElement.value = this.data['myAdLink'];
	}
}
