import { Component, EventEmitter, Output } from '@angular/core';
import { GetAdStatusListResponse } from '@core/models';
import { DestroyService } from '@shared/services/destroy.service';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { Observable, takeUntil } from 'rxjs';

@Component({
    selector: 'findqo-my-ad-headers-v2',
    templateUrl: './my-ad-headers-v2.component.html',
    styleUrls: ['./my-ad-headers-v2.component.scss'],
    standalone: false
})
export class MyAdHeadersv2Component {
	constructor(
		private destroyService: DestroyService,
		private myAdsFacade: MyAdsFacade
	) {
		this.adStatusList$ = this.myAdsFacade.getAdsStatusList$();
	}

	@Output() tabChange: EventEmitter<number> = new EventEmitter<number>();

	adStatusList$: Observable<GetAdStatusListResponse[]>;
	myAdHeaderTabs: GetAdStatusListResponse[];
	readonly sold = 'sold';

	onTabChanged(tabIndex: number): void {
		this.tabChange.emit(tabIndex);
	}

	ngOnInit(): void {
		this.myAdsFacade.loadAdStatusList();

		this.adStatusList$.pipe(takeUntil(this.destroyService)).subscribe((adStatusList: GetAdStatusListResponse[]) => {
			const MY_ADS_HEADER_STATUS = [
				'all',
				'live',
				'under-review',
				'edit-under-review',
				'draft',
				'unpublished',
				'sold',
				'rejected',
				'payment-failed',
				'payment-pending',
				'expired',
				'property-occupied',
				'sale-agreed',
			];

			this.myAdHeaderTabs = MY_ADS_HEADER_STATUS.map((reference) => {
				const AD_STATUS_ITEM = adStatusList.find((dataItem) => dataItem.reference === reference);
				return {
					reference,
					name: AD_STATUS_ITEM?.name || '',
					adCount: AD_STATUS_ITEM?.adCount || '0',
				};
			});
		});
	}
}
