import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ad-cancel-edit-dialog',
    templateUrl: './my-ad-cancel-edit-dialog.component.html',
    styleUrls: ['./my-ad-cancel-edit-dialog.component.scss'],
    standalone: false
})
export class MyAdCancelEditDialogComponent {
	constructor(private myAdsFacade: MyAdsFacade, @Inject(MAT_DIALOG_DATA) public data: any, public dialogRef: MatDialogRef<MyAdCancelEditDialogComponent>) {}

	readonly buttonType = ButtonType;

	onCloseDialog(): void {
		this.dialogRef.close();
	}

	cancelEditAd(): void {
		this.myAdsFacade.deleteAd<MyAdCancelEditDialogComponent>(this.data.adId, this.dialogRef, this.data.myAdHeaderStatusId);
	}
}
