import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IconType } from '@core/enums';
import { Translations } from '@core/models/translations.model';
import { IconService } from '@core/services/icon.service';
import { ViewportService } from '@core/services/viewport.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ads-filter',
    templateUrl: './my-ad-filter.component.html',
    styleUrls: ['./my-ad-filter.component.scss'],
    standalone: false
})
export class MyAdsFilterComponent {
	constructor(public viewport: ViewportService, public icon: IconService) {}

	@Input() translations: Translations;
	@Output() selectAds = new EventEmitter();
	@Output() clearSelection = new EventEmitter();

	readonly buttonType = ButtonType;
	readonly iconType = IconType;

	toggleAdsSelection = false;
	isCancelselection = false;
	selectedAd: string;
	selectedSection: string;

	// Mock data is being used with the values present in figma
	adListItems = [
		{ value: '0', viewValue: 'All Ads' },
		{ value: '1', viewValue: 'Live Ads' },
		{ value: '2', viewValue: 'Under Review Ads' },
		{ value: '3', viewValue: 'Pending Payment Ads' },
		{ value: '4', viewValue: 'Payment Failed Ads' },
	];

	adListSection = [
		{ value: '0', viewValue: 'All Section' },
		{ value: '1', viewValue: 'Motors' },
		{ value: '2', viewValue: 'Properties' },
	];

	onSelectAd(): void {
		this.toggleAdsSelection = !this.toggleAdsSelection;
		this.isCancelselection = true;
		this.selectAds.emit(this.toggleAdsSelection);
	}

	cancelSelection(): void {
		this.toggleAdsSelection = !this.toggleAdsSelection;
		this.isCancelselection = false;
		this.selectAds.emit(this.toggleAdsSelection);
		this.clearSelection.emit();
	}
}
