import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Translations } from '@core/models/translations.model';
import { ViewportService } from '@core/services/viewport.service';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import { IconService } from '@core/services/icon.service';

@Component({
    selector: 'findqo-my-ad-item',
    templateUrl: './my-ad-item.component.html',
    styleUrls: ['./my-ad-item.component.scss'],
    standalone: false
})
export class MyAdsInfoComponent {
	constructor(public viewport: ViewportService, public icon: IconService) {}

	@Input() index: string;
	@Input() isChecked: boolean;
	@Input() isToggleSelection: boolean;
	@Input() translations: Translations;
	@Input() myAd;
	@Output() checkboxChange = new EventEmitter<{ index: string; value: boolean }>();

	datePosted: string;

	onAdSelection(checked: boolean): void {
		this.checkboxChange.emit({ index: this.index, value: checked });
	}

	ngOnInit(): void {
		dayjs.extend(relativeTime);
		this.datePosted = dayjs(+this.myAd?.createdAt * 1000).fromNow();
	}
}
