import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ad-occupied-dialog',
    templateUrl: './my-ad-occupied-dialog.component.html',
    styleUrls: ['./my-ad-occupied-dialog.component.scss'],
    standalone: false
})
export class MyAdOccupiedDialogComponent {
	constructor(@Inject(MAT_DIALOG_DATA) public data: any, public dialogRef: MatDialogRef<MyAdOccupiedDialogComponent>, private myAdsFacade: MyAdsFacade) {}

	readonly iconType = IconType;
	readonly buttonType = ButtonType;

	onCloseDialog(): void {
		this.dialogRef.close();
	}

	markAsSold(): void {
		this.myAdsFacade.soldAd(this.data.adId, this.dialogRef, this.data.myAdHeaderStatusId);
	}
}
