import { Component, OnInit } from '@angular/core';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MY_ADS_HEADER_STATUS_MAPPING } from '@core/constants/my-ads.constant';
import { FeatureVersion } from '@core/enums';
import { Ad, PageMetaData, Translations } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { FilterStatusPipe } from '@shared/pipes/my-ads/filter-status.pipe';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';
import { IconService } from '@core/services/icon.service';
import { ButtonType } from '@shared/components/button/button.component';
import { CommonPlaceAdService } from '@core/services/common-place-ad.service';

@Component({
    templateUrl: './my-ad-list.page.html',
    styleUrls: ['./my-ad-list.page.scss'],
    providers: [DestroyService, FilterStatusPipe],
    standalone: false
})
export class MyAdListPage implements OnInit {
	constructor(
		public viewport: ViewportService,
		public icon: IconService,
		private destroyService: DestroyService,
		private myAdsFacade: MyAdsFacade,
		private filterStatusPipe: FilterStatusPipe,
		private sharedAdsFacade: SharedAdsFacade,
		private commonPlaceAdService: CommonPlaceAdService
	) {
		this.myAds$ = this.sharedAdsFacade.getAdList$();
		this.pageMetaData$ = this.sharedAdsFacade.getPageMetaData$();
	}

	translations$: Observable<Translations>;
	myAds$: Observable<Ad[]>;
	pageMetaData$: Observable<PageMetaData>;

	isSelectAd: boolean;
	isAllAdsSelected: boolean;
	isCheckAllAdSelected: boolean;
	numberOfAdsSelected: number;
	myAdListSkeletonArray = Array();
	headerTabIndex = 0;

	myAdList: Ad[];
	myAdListTemp: Ad[];

	readonly buttonType = ButtonType;
	readonly featureVersion = FeatureVersion;

	showSelectAd(toggleAdsSelection: boolean): void {
		this.isSelectAd = toggleAdsSelection;
	}

	handleAdSelection(event: { index: string; value: boolean }): void {
		const selectedAd = this.myAdList.find((element) => element.id === event.index);
		selectedAd.isChecked = event.value;

		this.numberOfAdsSelected = this.myAdList.filter((item) => item.isChecked).length;

		if (this.numberOfAdsSelected === this.myAdList.length) {
			this.isAllAdsSelected = true;
			this.isCheckAllAdSelected = true;
		} else {
			this.isAllAdsSelected = false;
			this.isCheckAllAdSelected = false;
		}
	}

	onSelectAllAds(isChecked: MatCheckboxChange): void {
		if (isChecked.checked) {
			this.myAdList = this.myAdList.map((adItem) => {
				return { ...adItem, isChecked: true };
			});

			this.numberOfAdsSelected = this.myAdList.length;
			this.isCheckAllAdSelected = true;
		} else {
			this.numberOfAdsSelected = 0;
			this.isCheckAllAdSelected = false;
			this.myAdList = this.myAdList.map((adItem) => {
				return { ...adItem, isChecked: false };
			});
		}
	}

	goToPlaceAd(): void {
		this.commonPlaceAdService.gotoPlaceAd();
	}

	unSelectAllAds(): void {
		this.myAdList = this.myAdList.map((adItem) => {
			return { ...adItem, isChecked: false };
		});

		this.isAllAdsSelected = false;
		this.isCheckAllAdSelected = false;
		this.numberOfAdsSelected = 0;
	}

	trackByAdId(_index: number, ad: any): string {
		return ad.id;
	}

	onPageChange(pageNumber: number): void {
		if (this.headerTabIndex === 0) {
			this.myAdsFacade.loadAdList({ page: pageNumber, perPage: 10 });
		} else {
			this.myAdsFacade.loadAdList({ status: MY_ADS_HEADER_STATUS_MAPPING[this.headerTabIndex], page: pageNumber, perPage: 10 });
		}
	}

	onTabchange(tabIndex: number): void {
		this.headerTabIndex = tabIndex;
		const TAB_NAME = MY_ADS_HEADER_STATUS_MAPPING[tabIndex];

		if (tabIndex === 0) {
			this.myAdsFacade.loadAdList({ page: 1, perPage: 10 });
		} else {
			this.myAdsFacade.loadAdList({ status: TAB_NAME, perPage: 10 });
		}
	}

	ngOnInit(): void {
		this.onTabchange(this.headerTabIndex);

		if (this.viewport.mobile) {
			this.myAdListSkeletonArray = Array(2);
		} else {
			this.myAdListSkeletonArray = Array(4);
		}

		this.myAds$.pipe(takeUntil(this.destroyService)).subscribe((adList: Ad[]) => {
			this.myAdList = adList.map((ad: Ad) => {
				return {
					...ad,
					isChecked: false,
				};
			});

			this.myAdListTemp = this.myAdList;
		});
	}
}
